import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, CustomInput, Row } from 'reactstrap';
import { InputGroupInline, InputTooltipProps, InputType, parseRefToDate, WindowComponent, WindowComponentProps, withTooltip } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { FICHA_FINANCEIRA_OPCAO_FILTRO_ENUM_VALUES, TODOS } from '../../../../../models/enumerated/mfp/fichaFinanceiraOpcaoFiltroEnum';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import EmpresaIS from '../../../../components/ger/empresaIS/EmpresaIS';
import TomadorServicoIS from '../../../../components/mfp/tomadorServicoIS/TomadorServicoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, FichaFinanceiraRV, REPORT_EVENT, REPORT_TITLE } from './fichaFinanceiraRV';

const DEFAULT_VALUES: FichaFinanceiraRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const FichaFinanceiraReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const { globalParameter } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<FichaFinanceiraRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });
  const [isGerarPorEmpresas, setIsGerarPorEmpresas] = useState();
  const labelWidth = 200;

  const onChangeGerarPorEmpresas = () => {
    formProps.setValue('empresasSelecionadas', []);
    setIsGerarPorEmpresas(formProps.getValues().gerarPorEmpresas);
  };

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue(
          'ano',
          parseRefToDate(globalParameter.empresaMFP.referencia, 0)
            .getFullYear()
            .toString()
        );
        formProps.setValue('empresasSelecionadas', []);
        formProps.setValue('filtro', TODOS.value, true);
      }, 500);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const propToolTip = (messagem: string): InputTooltipProps => {
    return {
      message: messagem,
      placement: 'top-start',
    };
  };

  const validateExtras = () => {
    const erros = [];

    if (isGerarPorEmpresas && formProps.getValues().empresasSelecionadas.length === 0) {
      erros.push("Campo 'Empresas' não pode estar em branco quando a opção 'Gerar por Empresas' estiver selecionada.");
    }

    return erros;
  };

  return (
    <WindowComponent
      title="Ficha Financeira"
      useGlobalParameter={true}
      globalParameter={globalParameter}
      dependencies={[() => hasEmpresa(globalParameter), () => hasEmpresaMFP(globalParameter), () => hasGrupoEmpresaConfig(globalParameter)]}
      wikiId={1923}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <Row>
          <Col className="col-3" style={{ marginTop: '10px' }}>
            <CustomInput
              type="switch"
              id="gerarPorEmpresas"
              name="gerarPorEmpresas"
              label="Gerar por Empresas"
              innerRef={formProps.register}
              onChange={onChangeGerarPorEmpresas}
            />
          </Col>
          <Col className="col-9">
            <EmpresaIS
              labelWidth={labelWidth}
              formProps={formProps}
              label="Empresa(s)"
              placeholder="Selecione no mínimo uma empresa"
              id="empresasSelecionadas"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'No mínimo uma Empresa deve ser selecionada' }}
              disabled={!isGerarPorEmpresas}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              id="ano"
              label="Ano"
              isPrependLabel={true}
              formProps={{ ...formProps, validation: { min: 1930, max: 3000, required: true } }}
              type={InputType.NUMBER}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <VinculoIS
              labelWidth={labelWidth}
              id="trabalhadores"
              formProps={formProps}
              label="Trabalhadores"
              placeholder="Selecione os Trabalhadores"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum trabalhador for selecionado todos serão utilizados.' }}
              disabled={isGerarPorEmpresas}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <TomadorServicoIS
              labelWidth={labelWidth}
              id="tomadores"
              formProps={formProps}
              label="Tomadores de Serviço"
              placeholder="Selecione os Tomadores"
              isPrependLabel={true}
              inputSearchProps={{ selectProps: { isMulti: true } }}
              tooltipProps={{ message: 'Se nenhum tomador for selecionado todos serão utilizados.' }}
              disabled={isGerarPorEmpresas}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              labelWidth={labelWidth}
              id="filtro"
              label="Filtro"
              isPrependLabel={true}
              placeholder="Selecione o filtro"
              formProps={{ ...formProps, validation: { required: true } }}
              inputDropDownProps={{
                optionsProvider: FICHA_FINANCEIRA_OPCAO_FILTRO_ENUM_VALUES,
                selectProps: { isClearable: false },
              }}
              type={InputType.DROP_DOWN}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <CustomInput
              type="switch"
              id="idexibirRegimeCaixa"
              name="exibirRegimeCaixa"
              label="Agrupar pela data de pagamento (Regime de Caixa)"
              innerRef={formProps.register}
            />
          </Col>
          {withTooltip(
            propToolTip(
              'Caso seja marcado, o sistema irá agrupar os eventos pela data de pagamento. Caso contrário, ele agrupa pela referência do cálculo.'
            ),
            'exibirRegimeCaixa'
          )}
        </Row>
        <br />
      </ReportForm>
    </WindowComponent>
  );
};

export default FichaFinanceiraReportView;
